.recipes {
    width: 90vw;
    display: grid;
    grid-template-columns: repeat(auto-fit, 400px);
    justify-content: space-evenly;
}

.sort-container {
    display: flex;
    width: 83.5vw;
    justify-content: flex-end;
    margin: 20px;
}

label {
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    padding-right: 10px;
}

select {
    cursor:pointer;
    border: none;
    font-size: 18px;
}

@media (max-width: 500px) {
    .recipes {
        justify-content: space-around;
    }
    .sort-container {
      display: none;
    }
    
    .layout-children {
      display: unset;
    }
}
.recipe {
  display: grid;
  grid-template-rows: 80% 10% 10%;
  grid-gap: 3%;
  grid-template-columns: 1fr;
  align-items: center;
  margin: 10px;
  box-shadow: 0px 0px 3px 1px #cecece;
  transition: 250ms all;
  padding: 20px;
  padding-bottom: 30px;
  margin-bottom: 60px;
}

.recipe:hover {
  transform: scale(1.02);
  opacity: 0.9;
}

.recipe-image {
  width: 300px;
  height: 199px;
  justify-self: center;
  box-shadow: 10px 10px 10px lightgray;
}

.recipe-name {
  font-size: 24px;
  grid-row: 2 / span 1;
  justify-self: center;
  font-weight: 600;
}

.cook-diff-div {
  display: grid;
  grid-gap: 15px;
  font-size: 1.5em;
  width: 100%;
  text-align: center;
}
.cook-time {
  background-color: rgb(243,243,243);
  margin: 0;
}
.difficulty1 {
  background-color: rgb(243,243,243);
  grid-column-end: 3;
  margin: 0;
  text-transform: uppercase;
}

.layout {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.layout-children {
  flex: 2 1;
  margin: auto;
  margin-top: 5%;
}

@media (max-width: 500px) {
  .layout {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .layout-children {
    width: 100vw;
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

